import React, { Component } from 'react';
import { Grid, Paper, Typography } from '@material-ui/core'
import Cloudy from './assets/images/cloudy.png'
import Rainy from './assets/images/rainy.png'
import Sunny from './assets/images/sunny.png'
import Snow from './assets/images/snow.png'

class Weather extends Component {
    state = { 
        temperature:[
            {id:1, day:'Wednessday', max: 78, min: 67, type:'sunny'},
            {id:2, day:'Thursday', max: 83, min: 66, type:'rainy'},
            {id:3, day:'Friday', max: 77, min: 65, type:'cloudy'},
            {id:4, day:'Saturday', max: 78, min: 64, type:'snowy'},
            {id:5, day:'Sudnay', max: 77, min: 62, type:'cloudy'},
            {id:6, day:'Monday', max: 71, min: 61, type:'cloudy'},
            {id:7, day:'Tuesday', max: 70, min: 60, type:'snowy'},
            {id:8, day:'Wednessday', max: 73, min: 60, type:'rainy'},
        ],
        logo:{
            'sunny': `${Sunny}`,
            'rainy': `${Rainy}`,
            'cloudy': `${Cloudy}`,
            'snowy': `${Snow}`,
        }
     }
    render() { 
        console.log(this.state.logo);
        return (  
            <React.Fragment>
                <br /><br />
                <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Grid container justify="center" spacing={2}>
                    {this.state.temperature.map(value => (
                        <Grid key={value.id} item>
                        <Paper>
                            <div style={{padding:20}}>
                                <img src={this.state.logo[value.type]} style={{width:100, height:100}}/>
                                <Typography>{value.type}</Typography>
                                <span style={{textAlignLast:'left'}}>{value.max}&deg;</span>
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                <span style={{textAlignLast:'right'}}>{value.min}&deg;</span>
                            </div>
                        </Paper>
                        </Grid>
                    ))}
                    </Grid>
                </Grid>
                </Grid>
            </React.Fragment>
        );
    }
}
 
export default Weather;