import React, { Component } from 'react';
import { Grid, Paper, Typography } from '@material-ui/core'
import Cloudy from './assets/images/cloudy.png'
import Rainy from './assets/images/rainy.png'
import Sunny from './assets/images/sunny.png'
import Snow from './assets/images/snow.png'

class Weather extends Component {
    state = { 
        data:{list:[]},
        logo:{
            "Clear": `${Sunny}`,
            'Rain': `${Rainy}`,
            'Clouds': `${Cloudy}`,
            'Snow': `${Snow}`,
        }
     }
     
    componentDidMount() {
        fetch('http://api.openweathermap.org/data/2.5/forecast?id=524901&APPID=8982243ef9a44671143fbbc01857ebf9')
        .then(res=>res.json())
        .then(data=>{
            this.setState({data:{list:data.list}}, ()=>console.log("donatto", this.state.data.list))
        })
        
    }

    render() { 
        console.log(this.state.logo);
        return (  
            <React.Fragment>
                <br /><br />
                <Grid container spacing={2}>
                <Grid item xs={12}>
                    <Grid container justify="center" spacing={2}>
                    {this.state.data.list.length>0?

                        <React.Fragment>
                            {this.state.data.list.map(value => (
                                <Grid key={value.dt} item>
                                <Paper>
                                    <div style={{padding:20}}>
                                        <img src={this.state.logo[value.weather[0].main]} style={{width:100, height:100}}/>
                                        <Typography>{value.weather[0].description}</Typography>
                                        <span style={{textAlignLast:'left'}}>{value.main.temp_max}&deg;</span>
                                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                        <span style={{textAlignLast:'right'}}>{value.main.temp_min}&deg;</span>
                                    </div>
                                </Paper>
                                </Grid>
                            ))}
                        </React.Fragment>
                        :<h1 style={{color:'red'}}>Loading Data . . .</h1>}
                    </Grid>
                </Grid>
                </Grid>
            </React.Fragment>
        );
    }
}
 
export default Weather;